<template>
	<div class="profile-account">
		<div class="profile-banner">
			<div class="profile-banner-assets">
				<div class="profile-cover">
				</div>

				<div class="profile-avatar-wrap">
					<div class="profile-avatar">
						<svg height="100%" width="100%">
							<circle cx="142" cy="142" r="134" transform="rotate(268 142 142)" />
							<image y="0" x="0" width="100%" height="100%" xlink:href="../assets/images/myopera-logo.png" />
						</svg>
						
					</div>
					<i class="fa fa-ellipsis-h fa-2x is-darkgray profile-options"></i>
				</div>
				<div class="xs-visible sm-hide profile-options-xs"><i class="fa fa-ellipsis-h fa-3x is-darkgray opions-icon-xs"></i></div>
			</div>

			<div class="profile-banner-text">
				<h1>{{ profile.name }}</h1>
				<p><span>{{ profile.city }}, {{ profile.province }} </span><br class="xs-break" /><span class="is-golden">{{ profile.connectionStrength }}% {{ profile.connectionLevel | to-uppercase }}</span></p>
				<p>{{ profile.type }} Opera Company based in {{ profile.country }} |<br class="xs-break" /> Member of {{ profile.membership }}</p>
			</div>
		</div>

		<company-tools v-bind:company-events="profile.events" class="profile-section"></company-tools>

		<profile-connctions class="profile-section"></profile-connections>

		<company-auditions class="profile-section"></company-auditions>
			
		<profile-posts :name="profile.name" :posts="profile.posts"></profile-posts>
	</div>
</template>

<script>
import tools from './tools';
import auditions from './auditions'
import posts from './posts'
import connections from './connections'

export default {
	components: {
		'company-tools': tools,
		'company-auditions': auditions,
		'profile-posts': posts,
		'profile-connections': connections
	},
	data() {
		return {
			profileType: "",
			connections: []
		}
	},
	computed: {
		profile() {
			return this.$store.state.company;
		}
	},
	created () {
		this.connections = this.profile.connections.slice(0, 4);
		console.log(this.connections);

		// find out all voice types
		// const voices = []
		// for (var i=0; 1< this.profile.auditionCandidates.length; i++) {
		// 		voices.push(this.profile.auditionCandidates[i].voice_type);
		// 		console.log(voices);
		// };
	}
}
</script>

<style lang="scss" scoped>
@import '../styles/style-variables.scss';

.profile-account {
	grid-area: main;
}

circle {
  fill: white;
  stroke: #cd9d2b;
  stroke-width: 15;
  stroke-dasharray: 841.946;
  stroke-dashoffset: 210.487;
  animation: dash 2s linear reverse;
}

@keyframes dash {
	to {
		stroke-dashoffset: 841.946;
	}
}

.profile-banner {
	display: block;
	margin-bottom: 24px;
	background-color: white;
	height: 100%;
}

.profile-banner-assets {
	box-sizing: border-box;
	display: block;
	height: 573px;
	margin-bottom: 47px;
	position: relative;
}

.profile-cover {
	width: 100%;
	background-image: url('../assets/images/myoperacover.jpg');
	background-color: white;
	height: 434px;
	overflow: hidden;    
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.profile-avatar-wrap {
	height: 283px;
	position: absolute;
	top: 45%;
	width: 100%;
}

.profile-avatar {
	border-radius: 50%;
	height: 285px;
	width: 285px;
	margin: 0 5px 0px 50%;
	transform: translateX(-50%);
	background: transparent;
	display: inline-flex;
	align-items: center;
}

.profile-options {
	display: inline-block;
	transform: translateX(-130px);
}

.profile-banner-text {
	text-align: center;
	margin-bottom: 55px;
}

.profile-section {
	background-color: white;
	padding: 40px 30px 30px 100px;
	margin: 0px 0px 24px 0px;
}

.profile-section h2 {
	margin-top: 0px;
}

.sm-hide {
	display: none;
}

/*for phone-only*/

@media screen and (max-width: 46rem) {
	.profile-account {
		width: 100%;
	}

	.profile-section {
		width: 100%;
		padding: 50px 28px 28px 28px;
		margin: 0px 0px 24px 0px;
	}

	.profile-options-xs {
	    margin: 125px;
	    display: block;
	    text-align: center;
  	}

	.slide {
		overflow-x: auto;
		flex-wrap: nowrap;
		-webkit-overflow-scrolling: touch;
	  	-ms-overflow-style: -ms-autohiding-scrollbar;
	}

}

</style>



